
        *{margin:0;padding:0;}
        #html{
            word-break:break-all;
        }
        #div1{
        position:fixed;
        left: 50%;
        transform:translateX(-50%);
        top:20px;
    }
    #div1::before{
    content:'';
    display:block;
    /* border:1px solid red; */
    position:absolute;

}
    #div1::after
    {
    content:'';
    display:block;
    /* border:1px solid red; */
    position:absolute;
    }
    @media(max-width:500px)
    {
       #html{
           height:50vh;
           overflow:auto;
       }
       #div1Wrapper{
           height:50vh;
           
       }
       #div1{
        position:relative;
        top:10px;
        right:0;
        left: 50%;
        transform:translateX(-50%);
       }
    }
